<template>
  <div class="page_wrapper" v-show="pageMixin_showPage">


    <div class="main_body animated  fadeIn">

      <el-row class="head_area  panel_area">
        <el-col :span="24">
          <span class="title">微信红包</span>
          <span class="description">微信红包发放记录查询</span>
        </el-col>
        <!--        <el-col :span="6">-->

        <!--          <el-button @click="openDialog" size="mini" type="success">-->
        <!--            <img alt="" src="../../assets/other-img/wechat.png" style="width: 20px;vertical-align: middle">-->
        <!--            微信实时单查-->
