        <!--        </el-col>-->
      </el-row>


      <el-row class="panel_area">


        <el-col :span="24">
          <TableToolsBox :show-add="false" :show-chart="false" :show-down-load="true" @download="downloadFn()"
                         @refresh="refresh"
                         @search="searchFn">
            <template slot="search">

              <el-select class="search_param" placeholder="请选择状态" style="width: 150px"
                         v-model="searchParameterRes.status">
                <el-option :key="index" :label="item.text"
                           :value="item.value"
                           v-for="(item,index) of this.searchParameterSource.status"></el-option>
              </el-select>


              <el-input class="input-with-select search_param" clearable placeholder="请输入内容"
                        style="width: 250px"
                        v-model.trim="searchParameterRes.searchValue">
                <el-select placeholder="请选择" slot="prepend" style="width: 120px"
                           v-model="searchParameterRes.searchBy">
                  <el-option :key="index" :label="item.text"
                             :value="item.value "
                             v-for="(item,index) of this.searchParameterSource.searchBy"></el-option>
                  <!--                  <el-option label="用户id" value="userid"></el-option>-->
                  <!--                  <el-option label="用户名" value="username"></el-option>-->
                </el-select>
              </el-input>
              <el-date-picker
                align="right"
                class="search_param"
                end-placeholder="结束日期"
                range-separator="-"
                start-placeholder="开始日期"
                style="width: 250px"
                type="daterange"
                unlink-panels
                v-model="searchParameterRes.dateRange"
                value-format="timestamp">
              </el-date-picker>

            </template>
          </TableToolsBox>
        </el-col>
      </el-row>


      <el-row :gutter="20" class="panel_area">
        <el-col :span="20">
          <el-card class="box-card padding_none">
            <el-table
              :data="tableData"
              :expand-row-keys="tableExpandArr"
              :row-style="{cursor: 'pointer'}"
              @row-click="tableRowClickFn"
              @sort-change="tableSortFn"
              border
              row-key="id"
              stripe
              style="width: 100%"
            >
              <el-table-column type="expand">
                <template slot-scope="props">
                  <el-form class="table-expand" label-position="left">
                    <el-form-item label="ID">
                      <span>{{ props.row.id }}</span>
                    </el-form-item>
                    <el-form-item label="用户ID">
                      <el-button @click="globalMixin_jump('PlayerInfo',{user_id:props.row.user_id})" plain
                                 size="mini"
                                 type="primary">{{props.row.user_id}}
                      </el-button>
                    </el-form-item>
                    <el-form-item label="用户昵称">
                      <span>{{ props.row.player_name }}</span>
                    </el-form-item>
                    <el-form-item label="OPENID">
                      <span>{{ props.row.re_openid }}</span>
                    </el-form-item>
                    <el-form-item label="APPID">
                      <span>{{ props.row.wxappid }}</span>
                    </el-form-item>
                    <el-form-item label="UNIONID">
                      <span>{{ props.row.union_id }}</span>
                    </el-form-item>

                    <el-form-item label="电话号码">
                      <span>{{ props.row.tel }}</span>
                    </el-form-item>
                    <el-form-item label="金额">
                      <span>{{ props.row.amount }}</span>
                    </el-form-item>
                    <el-form-item label="状态">
                      <span>{{ props.row.status }}</span>
                    </el-form-item>

                    <el-form-item label="微信错误码">
                      <span>{{ props.row.err_code }}</span>
                    </el-form-item>

                    <el-form-item label="微信错误码描述">
                      <span>{{ props.row.err_code_des }}</span>
                    </el-form-item>

                    <el-form-item label="微信商户订单号">
                      <span>{{ props.row.mch_billno }}</span>
                      <el-button @click="openDialog(props.row.mch_billno)" size="mini" type="success">
                        <img alt="" src="../../assets/other-img/wechat.png" style="width: 20px;vertical-align: middle">
                      </el-button>
                    </el-form-item>

                    <el-form-item label="创建时间">
                      <span>{{ props.row.create_time }}</span>
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column
                align="center"
                label="#"
                type="index"
                width="50">
              </el-table-column>


              <!--              <el-table-column-->
              <!--                label="ID"-->
              <!--                prop="id"-->
              <!--              ></el-table-column>-->
              <!--            <el-table-column-->
              <!--              label="用户ID"-->
              <!--              prop="user_id"-->
              <!--            >-->
              <!--            </el-table-column>-->
              <el-table-column

                label="用户昵称"
                prop="player_name"
              >
              </el-table-column>
              <!--            <el-table-column-->
              <!--              align="center"-->
              <!--              label="OPENID"-->
              <!--              prop="re_openid"-->

              <!--            >-->
              <!--            </el-table-column>-->
              <el-table-column

                align="center"
                label="电话号码"
                prop="tel"
                width="150"
              >
              </el-table-column>
              <el-table-column
                align="center"
                label="金额"
                prop="amount"
              >
              </el-table-column>
              <el-table-column
                align="center"
                column-key="status"
                label="状态"
                prop="status"
                width="200"

              >
              </el-table-column>


              <el-table-column
                align="center"
                label="创建时间"
                prop="create_time"
                sortable="custom"
                width="180">
              </el-table-column>


              <!--            <el-table-column-->
              <!--              align="center"-->
              <!--              fixed="right"-->
              <!--              label="操作"-->
              <!--              width="100">-->

              <!--              <template slot-scope="scope">-->
              <!--                <el-button @click="openDialog('edit',scope.row)" size="small" type="text">编辑</el-button>-->

              <!--                <el-button @click="openDialog('delete',scope.row)" size="small" type="text">删除</el-button>-->
              <!--              </template>-->
              <!--            </el-table-column>-->

            </el-table>
          </el-card>


        </el-col>
        <el-col :span="4">
          <el-card class="box-card  money_count panel_area">
            <div class="clearfix" slot="header">
              <h6>当前条件金额合计</h6>
            </div>
            <b>{{addition.currentMoney}} 元</b>

          </el-card>

          <el-card class="box-card  money_count panel_area">
            <div class="clearfix" slot="header">
              <h6>历史发放金额合计</h6>
            </div>
            <b> {{addition.allMoney}} 元</b>
          </el-card>
          <el-card class="box-card money_count panel_area">
            <div class="clearfix" slot="header">
              <h6>按状态查看金额</h6>
            </div>
            <ul>
              <li :key="index" class="text item" v-for="(item,index) of addition.allStatusMoney">
                {{ item.text}}: <span>{{item.money}}元</span>
              </li>
            </ul>
          </el-card>
        </el-col>


      </el-row>


      <el-row class="panel_area">
        <el-pagination
          :current-page="tableMixin_currentPage"
          :page-size="tableMixin_pageSize"
          :page-sizes="tableMixin_pageSizeArr"
          :total="tableMixin_total"
          @current-change="tableMixin_handleCurrentChange"
          @size-change="tableMixin_handleSizeChange"
          layout="total, sizes, prev, pager, next, jumper">
        </el-pagination>
      </el-row>


      <el-dialog :append-to-body="true"
                 :close-on-click-modal="false"
                 :title="dialogTitle"
                 :visible.sync="dialogVisible">
        <div class="dialog_wrap">
          <el-form>
            <el-form-item label="红包金额">
              <span>{{ dialogItem.total_amount }}(单位:分)</span>
            </el-form-item>
            <el-form-item label="红包个数">
              <span>{{ dialogItem.total_num }}</span>
            </el-form-item>
            <el-form-item label="红包状态">
              <span>{{ dialogItem.status }}</span>

              <!--              <ul style="color: gray;font-size: 14px">-->
              <!--                <li>SENDING:发放中</li>-->
              <!--                <li>SENT:已发放待领取</li>-->
              <!--                <li>FAILED：发放失败</li>-->
              <!--                <li>RECEIVED:已领取</li>-->
              <!--                <li>RFUND_ING:退款中</li>-->
              <!--                <li>REFUND:已退款</li>-->
              <!--              </ul>-->

            </el-form-item>
            <el-form-item label="发送时间">
              <span>{{ dialogItem.send_time }}</span>
            </el-form-item>
            <el-form-item label="错误代码描述">
              <span>{{ dialogItem.err_code_des }}</span>
            </el-form-item>
            <!--            <el-form-item label="领取时间">-->
            <!--              <span>{{ dialogItem.rcv_time	 }}</span>-->
            <!--            </el-form-item>-->
          </el-form>
        </div>

      </el-dialog>


    </div>

  </div>
</template>

<script>
    import axios from 'axios';
    import pageMixin from "@/mixin/pageMixin";
    import tableMixin from "@/mixin/tableMixin";
    import TableToolsBox from "../childComponents/TableToolsBox";

    export default {
        name: "WechatRedPackage",
        components: {
            TableToolsBox,
        },
        mixins: [pageMixin, tableMixin],
        props: {},
        data() {
            return {
                dialogVisible: false,
                mch_billno: "",//微信订单号
                dialogTitle: "",
                dialogItem: {
                    total_amount: '',
                    total_num: '',
                    send_time: '',
                    status: '',
                    rcv_time: '',
                    err_code_des: ''//错误代码描述
                },
                dialogItemStatusZh: {
                    SENDING: '发放中',
                    SENT: '已发放待领取',
                    FAILED: '发放失败',
                    RECEIVED: '已领取',
                    RFUND_ING: '退款中',
                    REFUND: '已退款',
                },
                tableData: [],//表格数据
                tableExpandArr: [],//表格要展开的列
                tableSort: {
                    sortBy: '',//通过该列的 字段 进行排序
                    sort: "desc",//排序方式
                },
                searchParameterSource: {
                    status: [],//状态 下拉框 数据源
                    searchBy: [],//搜索字段 下拉框 数据源
                },
                searchParameterRes: {
                    status: "",//状态
                    searchBy: '',//通过该字段搜索
                    searchValue: '',//搜索值
                    dateRange: '',//时间区间
                },
                addition: {
                    currentMoney: 0,
                    allStatusMoney: [],
                    allMoney: 0
                }//一些附带参数   金额统计用的
            }
        },
        watch: {},
        computed: {},
        methods: {
            /**
             * 点击表格row  触发
             * @param row
             * @param column
             * @param event
             */
            tableRowClickFn(row, column, event) {
                if (this.tableExpandArr.indexOf(row.id) === -1) {
                    this.tableExpandArr = [];
                    this.tableExpandArr.push(row.id)
                } else {
                    this.tableExpandArr = [];
                }

            },
            /**
             * 点击表格排序 触发
             * @param ob
             */
            tableSortFn(ob) {
                this.tableSort.sortBy = ob.prop;
                ob.order === 'ascending' ? this.tableSort.sort = 'asc' : this.tableSort.sort = 'desc';
                this.getTableDataSource();
            },


            /**
             * 获取 搜索相关的参数 下拉框等的 待选值
             */
            getSearchParameters() {

                this.globalMixin_request('/wechat_red_package_search_parameters', "get").then(res => {
                    const {code, data, message} = res;
                    if (code === 10000) {
                        this.searchParameterSource.status = data.status || [];
                        this.searchParameterSource.searchBy = data.searchBy || [];
                        this.searchParameterRes.searchBy = this.searchParameterSource.searchBy[0].value; //设置下拉框默认选中第一个
                    }

                })


            },
            /**
             * 获取表格数据
             */
            getTableDataSource() {


                this.globalMixin_request('/wechat_red_package', "get", {
                    searchBy: this.searchParameterRes.searchBy,
                    status: this.searchParameterRes.status,
                    searchValue: this.searchParameterRes.searchValue,
                    startTime: this.searchParameterRes.dateRange[0],
                    endTime: this.searchParameterRes.dateRange[1],
                    sortBy: this.tableSort.sortBy,//排序字段
                    sort: this.tableSort.sort,//排序规则
                    currentPage: this.tableMixin_currentPage,
                    pageSize: this.tableMixin_pageSize
                }).then(res => {
                    const {code, data, message, addition} = res;
                    if (code === 10000) {
                        this.tableData = data.data;
                        this.tableMixin_total = data.total;
                        this.addition = addition;
                    }

                })

            },

            /**
             * 清除已选/已填写 的搜索参数
             */
            clearSearchParameterRes() {
                for (let p in this.searchParameterRes) {
                    if (this.searchParameterRes.hasOwnProperty(p)) {
                        this.searchParameterRes[p] = "";
                    }
                }

                this.searchParameterRes.searchBy = this.searchParameterSource.searchBy[0].value; //设置下拉框默认选中第一个
            },
            /**
             * 刷新
             */
            refresh() {
                this.resetPage();
                this.clearSearchParameterRes();
                this.getSearchParameters();
                this.getTableDataSource();
            },
            /**
             * 执行搜索
             */
            searchFn() {
                this.resetPage();
                this.getTableDataSource();
            },
            /**
             * 导出表格
             **/
            downloadFn() {

                const startTime = this.searchParameterRes.dateRange[0];
                const endTime = this.searchParameterRes.dateRange[1];

                if (startTime && endTime) {
                    this.ele_notify('稍等 表格开始导出');
                    window.location.href = window.API_server.API_ADDRESS + '/export_csv?type=wechat_redpackage&startTime=' + startTime + '&endTime=' + endTime + '&token=' + encodeURIComponent(this.$store.getters.getOperatorInfo.token);

                } else {

                    this.ele_alert('请先选择时间区间再导出', 'warning');


                }

            },
            /**
             * 重置页面
             */
            resetPage() {
                this.tableMixin_currentPage = 1;
                this.tableExpandArr = [];//清空 打开的row

            },
            openDialog(id) {
                this.dialogVisible = true;
                this.dialogTitle = "微信订单记录单独查询:" + id;

                for (let p in this.dialogItem) {
                    if (this.dialogItem.hasOwnProperty(p)) {
                        this.dialogItem[p] = "";
                    }
                }

                let http = axios.create({
                    baseURL: 'http://dzhall.uyqart.com',
                    timeout: 20000,
                    headers: {
                        "Content-Type": "application/json"
                    }
                });


                http.get('queryreden?mch_billno=' + id).then(res => {

                    this.dialogItem.send_time = res.data.send_time.send_time;
                    // this.dialogItem.status = res.data.status.status;
                    this.dialogItem.total_amount = res.data.total_amount.total_amount;
                    this.dialogItem.total_num = res.data.total_num.total_num;
                    this.dialogItem.err_code_des = res.data.err_code_des.err_code_des;
                    // this.dialogItem.rcv_time = res.data.rcv_time.rcv_time;

                    this.dialogItem.status = this.dialogItemStatusZh[res.data.status.status];
                }).catch(e => {

                    for (let p in this.dialogItem) {
                        if (this.dialogItem.hasOwnProperty(p)) {
                            this.dialogItem[p] = "获取失败";
                        }
                    }

                })
            }
        },
        created() {
            this.getSearchParameters();
            this.getTableDataSource();
        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
  @import "../../assets/styles/base";

  .table-expand {
    font-size: 0;
  }

  .table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .page_wrapper {

    .money_count {

      color: $baseColor2;

      b {
        color: gray;
        font-size: 30px;
        font-weight: normal;
      }

      h6 {
        font-weight: normal;
        color: $baseColor3;
      }

      li {
        color: gray;
        margin-bottom: 10px;
      }

    }


  }
</style>



